import React, { useState } from 'react'
import image from '../images/icon.png'
/*import image2 from '../images/multiply.png'*/
import { HashLink as Link } from 'react-router-hash-link'
export default function Nav2() {
    const [val,setval]=useState('none')
    let toggle=()=>{
        if( document.getElementById('nav2').style.display==='none'){
            document.getElementById('nav2').style.display=setval('flex');
            document.getElementById('nav2').style.transition='opacity 0.5s ease-in-out';
            document.getElementById('nav2').style.opacity='1';       
    }
        else{
            document.getElementById('nav2').style.display=setval('none')
    
        }
    }

    return (
        <div className="nav2">
        <button onClick={toggle} className="btn BTN">
            <img className='imagebtn' src={image} alt="" />
        </button>
         <ul style={{  display:val,
           transition: 'opacity 0.5s ease-in-out',
           opacity: val==='flex' ? 1 : 0}} id='nav2' className="list1"> 
            <li> <Link smooth to="#">Home</Link></li>
            <li> <Link smooth to='#about'>About Me</Link></li>
            <li> <Link smooth to="#skills">My Skills</Link></li>
            <li> <Link smooth to="#resume">Resume</Link></li>
            <li> <Link smooth to="#contact">Contact</Link></li>
          
        </ul>     
    </div>
  )
}
